<script>
import {mapGetters} from 'vuex'

    export default {

        components: {
        },

        data() {
            return {
            }
        },

        async created() {
            this.$emit('update:title', 'Кабинет')
            if (Object.keys(this.getUser).length == 0 || this.$route.params.id != this.getUser.user_id) {
                await this.$store.dispatch('login', this.$route.params.id)
                this.$fire({
                    title: "Уведомление",
                    text: "Добро пожаловать, " + this.getUser.name + " " + this.getUser.second_name,
                    type: "success",
                    timer: 2000
                });

                this.$router.push('/')
            }
        },

        computed: {
            ...mapGetters(['getUser', 'getRecentEvents']),
        },

        methods: {

            first_letters(name, surname) {
                let fl = surname[0]
                let sl = name[0]
                return fl+sl
            },

            async sortEvents() {
                this.getRecentEvents = this.getRecentEvents.sort(function (a, b) {
                    if (a.timeStart > b.timeStart) {
                        return 1;
                    }
                    if (a.timeStart < b.timeStart) {
                        return -1;
                    }
                    return 0;
                });
            },

            async getBlue() {

                await this.getRecentEvents.forEach(elem => {
                    if ((new Date(elem.timeStart) < Date.now()) && (new Date(elem.timeStop) > Date.now())) {
                        elem.isActive = true
                    }
                })
            },
        }
    }
</script>

